"use client"

import { useState, useEffect, useCallback } from "react"
import { useParams, useRouter } from "next/navigation"
import { Button } from "@/components/ui/button"
import { Card } from "@/components/ui/card"
import { Badge } from "@/components/ui/badge"
import { 
  ChevronLeft, Loader2, Sparkles, 
  CheckCircle2, AlertCircle, Timer, Trophy 
} from "lucide-react"

export default function PromotionDetailPage() {
  const params = useParams()
  const id = params.id as string
  const router = useRouter()
  
  const [loading, setLoading] = useState(true)
  const [submitting, setSubmitting] = useState(false)
  const [promotion, setPromotion] = useState<any>(null)
  const [result, setResult] = useState<any>(null)

  const BASE_URL = "http://34.50.7.8:30000/v1/promotions"

  // 1. 내 참여 결과 조회 (GET)
  const fetchMyResult = useCallback(async () => {
    const token = localStorage.getItem("accessToken")
    if (!token) return;
    
    try {
      const res = await fetch(`${BASE_URL}/${id}/participations`, {
        headers: { "Authorization": `Bearer ${token}` }
      })
      const data = await res.json()
      if (data.isSuccess) {
        setResult(data.result)
      }
    } catch (e) {
      console.error("결과 조회 실패", e)
    }
  }, [id])

  // 2. 프로모션 상세 정보 로드
  useEffect(() => {
    const token = localStorage.getItem("accessToken")
    
    // 전체 리스트에서 해당 ID 찾기 (단일 조회 API가 없을 경우 대비)
    fetch(`${BASE_URL}?page=0&size=100`, {
      headers: { "Authorization": `Bearer ${token}` }
    })
      .then(res => res.json())
      .then(data => {
        if (data.isSuccess) {
          const found = data.result.content.find((p: any) => p.id === id)
          setPromotion(found)
        }
        setLoading(false)
      })
      .catch(() => setLoading(false))

    fetchMyResult()
  }, [id, fetchMyResult])

  // 3. 참여 신청 (POST)
  const handleJoin = async () => {
    const token = localStorage.getItem("accessToken")
    setSubmitting(true)
    
    try {
      const res = await fetch(`${BASE_URL}/${id}/join`, {
        method: "POST",
        headers: { 
          "Authorization": `Bearer ${token}`,
          "Content-Type": "application/json"
        }
      })
      const data = await res.json()
      
      if (data.isSuccess) {
        alert("참여 신청이 완료되었습니다!")
        fetchMyResult() // 신청 후 즉시 결과 갱신
      } else {
        alert(data.message || "참여에 실패했습니다.")
      }
    } catch (e) {
      alert("신청 처리 중 오류가 발생했습니다.")
    } finally {
      setSubmitting(false)
    }
  }

  if (loading) return <div className="min-h-screen flex items-center justify-center bg-background"><Loader2 className="h-10 w-10 animate-spin text-primary" /></div>
  if (!promotion) return <div className="p-10 text-center">프로모션 정보를 불러올 수 없습니다.</div>

  return (
    <div className="min-h-screen bg-background text-foreground">
      <header className="border-b border-border bg-card/50 backdrop-blur-sm sticky top-0 z-50 h-16 flex items-center px-4">
        <Button variant="ghost" size="icon" onClick={() => router.back()} className="mr-2">
          <ChevronLeft className="h-5 w-5" />
        </Button>
        <span className="font-black italic uppercase tracking-tighter text-sm">Promotion Details</span>
      </header>

      <main className="container mx-auto px-4 py-10 max-w-2xl">
        <Card className="p-8 border border-border shadow-2xl bg-card relative overflow-hidden">
          <div className="absolute top-0 right-0 p-8 opacity-5 pointer-events-none">
            <Sparkles size={120} className="text-primary" />
          </div>

          <div className="relative z-10">
            <Badge className={`mb-4 border-none font-bold px-3 py-1 ${
              promotion.status === 'ACTIVE' ? 'bg-primary' : 'bg-slate-500'
            }`}>
              {promotion.status}
            </Badge>
            
            <h1 className="text-3xl font-black tracking-tight mb-2 leading-tight">
              {promotion.name}
            </h1>
            <p className="text-muted-foreground font-bold mb-8 opacity-70 italic text-sm">
              이벤트 기간: {promotion.startTime.split('T')[0]} ~ {promotion.endTime.split('T')[0]}
            </p>

            <div className="bg-secondary/30 rounded-3xl p-10 text-center mb-8 border border-border/50">
              <p className="text-[10px] font-black text-muted-foreground uppercase tracking-[0.2em] mb-3">Reward Points</p>
              <p className="text-5xl font-black text-primary tracking-tighter">
                {promotion.pointAmount.toLocaleString()}P
              </p>
            </div>

            {/* 참여 결과 UI */}
            {result ? (
              <div className={`rounded-2xl p-8 border-2 flex flex-col items-center text-center space-y-4 ${
                result.status === 'WIN' ? 'bg-green-500/5 border-green-500/20' : 
                result.status === 'LOSE' ? 'bg-slate-500/5 border-slate-500/20' : 
                'bg-orange-500/5 border-orange-500/20'
              }`}>
                {result.status === 'WIN' ? (
                  <>
                    <div className="h-16 w-16 bg-green-500 rounded-full flex items-center justify-center shadow-lg shadow-green-500/20">
                      <Trophy className="h-8 w-8 text-white" />
                    </div>
                    <div>
                      <h2 className="text-2xl font-black text-green-600 uppercase tracking-tight">당첨을 축하합니다!</h2>
                      <p className="text-sm font-bold text-green-600/70 mt-1">포인트가 자산으로 곧 합산됩니다.</p>
                    </div>
                  </>
                ) : result.status === 'LOSE' ? (
                  <>
                    <div className="h-16 w-16 bg-slate-400 rounded-full flex items-center justify-center">
                      <AlertCircle className="h-8 w-8 text-white" />
                    </div>
                    <div>
                      <h2 className="text-2xl font-black text-slate-600 uppercase tracking-tight">아쉬운 결과입니다</h2>
                      <p className="text-sm font-bold text-slate-500/70 mt-1">다음 프로모션에 다시 도전해주세요.</p>
                    </div>
                  </>
                ) : (
                  <>
                    <div className="h-16 w-16 bg-orange-500 rounded-full flex items-center justify-center animate-pulse">
                      <Timer className="h-8 w-8 text-white" />
                    </div>
                    <div>
                      <h2 className="text-2xl font-black text-orange-600 uppercase tracking-tight">참여 확인 중</h2>
                      <p className="text-sm font-bold text-orange-500/70 mt-1">결과를 집계하고 있습니다. 잠시만 기다려주세요.</p>
                    </div>
                  </>
                )}
              </div>
            ) : (
              <Button 
                size="lg" 
                className="w-full h-20 rounded-2xl font-black text-xl uppercase italic tracking-widest shadow-2xl shadow-primary/30 transition-all hover:scale-[1.02] active:scale-[0.98]"
                disabled={promotion.status !== "ACTIVE" || submitting}
                onClick={handleJoin}
              >
                {submitting ? (
                  <Loader2 className="h-6 w-6 animate-spin" />
                ) : (
                  <>
                    <CheckCircle2 className="mr-3 h-6 w-6" />
                    JOIN PROMOTION NOW
                  </>
                )}
              </Button>
            )}

            <p className="mt-8 text-center text-[11px] font-bold text-muted-foreground opacity-50 uppercase tracking-tighter">
              * This promotion is subject to early termination based on stock availability.
            </p>
          </div>
        </Card>
      </main>
    </div>
  )
}